import React from "react";
import classes from "./AboutUs.module.css";
import Info from "./Info";

let inState = true;

const AboutUs = ({ closeHandler, isVisible }) => {
  let className = classes.dropdown_content;

  if (isVisible) {
    className += " " + classes.animateFromTopToBottom;
  } else if (!inState) {
    className += " " + classes.animateFromBottomToTop;
  } else if (inState) {
    inState = false;
  }

  return (
    <div className={className}>
      <a className={classes.close_btn} onClick={closeHandler}>
        CLOSE
      </a>
      <div className={classes.about_content}>
        <h1 className={classes.heading}>About Time UP</h1>
        <p>
          Serving the Columbus Area, let us capture your swag. Add entertainment
          to your event - whatever it might be. We serve receptions, school
          events, birthdays, weddings, corporate events, parties you name it.
        </p>
        <p>Contact Us to Book Early</p>
      </div>
      <Info />
    </div>
  );
};

export default React.memo(AboutUs);
